    "Short description": "Indeed 1111 is a unique and spectacular venue. Designed by internationally renowned, Pritzker Prize winning architects Herzog &amp; de Meuron, 1111 Lincoln Road is a modernist masterpiece which elevates the senses and enhances the moment. The panoramic ocean, city and bay views enjoyed from our vantage point are sheer perfection, and offer an unparalleled glimpse of Miami that could happen no place else in the world. 1111 Lincoln Road has been recognized by one of the top 25 most architecturally significant buildings in the state of Florida. The soaring 34 foot ceilings, the expansive views and the lights of the city are mesmerizing. There's nothing else like it, and it's truly the quintessential Miami experience.\n\\n\n\\n[vc_empty_space height=\"30px\"][vc_btn title=\"Request Now\" style=\"flat\" shape=\"square\" color=\"chino\" size=\"lg\" link=\"url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|\"]",
