    "Visibility in catalog": "visible",
    "Short description": "The Bath Club is a hidden gem located on the beach in the beautiful City of Miami Beach. With over 26,000 square feet of event space, your guests will experience a different era as they enter our unique and charismatic rooms. From a simple yet distinctive cocktail to an 8-course sit-down meal, The Bath Club offers the ideal rental venue for an unforgettable event.\n\\n\n\\n[vc_empty_space height=\"30px\"][vc_btn title=\"Request Now\" style=\"flat\" shape=\"square\" color=\"chino\" size=\"lg\" link=\"url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|\"]",
