    "Short description": "The Temple House is a spectacular and exceptional event space. Designed by one of the most famous Art Deco architects in history, this remarkable building has been fully renovated and is available for private events. With approximately 16,000 square feet this year-round air-conditioned breathtaking facility features a Grand Room with 25 foot ceilings, a beautiful mezzanine, catering space, and more. The facility has served as a blank canvas for celebrity studded private events, corporate events, fundraisers, galas, weddings, product launches, art exhibits, film production, press &amp; media events, seminars and more.\n\\n\n\\n[vc_empty_space height=\"30px\"][vc_btn title=\"Request Now\" style=\"flat\" shape=\"square\" color=\"chino\" size=\"lg\" link=\"url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|\"]",
