    "Visibility in catalog": "visible",
    "Short description": "<p style=\"font-weight: 400; text-align: left;\">One of Miami's finest early 20th century Mediterranean Revival estates, Villa Woodbine is an award-winning private mansion, located in Coconut Grove, south of Downtown Miami on South Bayshore Drive. Exclusive to Bill Hansen Catering &amp; Event Production, the historic property is a treasure of a bygone era. With its enchanting garden, Villa Woodbine, sits just across from Biscayne Bay. The charming Villa is suitable for weddings, corporate and social events.</p>\n\\n[vc_empty_space height=\"30px\"][vc_btn title=\"Request Now\" style=\"flat\" shape=\"square\" color=\"chino\" size=\"lg\" link=\"url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|\"]",
