    "Short description": "We?ve coined the phrase ?Very Vizcaya? to describe an experience or situation that captures the complexity and contradiction of this landmark estate. Vizcaya is beautiful and serene. But it is also a place of intellectual stimulation, compelling us to consider the relationships between native and foreign, old and new, and natural and designed. The curiosity that brought you here today suggests you?re probably already forming your own opinion of why Vizcaya is special and worthy of our efforts to preserve it. Vizcaya has been described as ?the finest private house ever built in America,? ?a jewel among Miami?s skyscrapers and subdivisions,? and ?some Noah?s Ark of decorative arts,? its exteriors offering the ?manic tranquility of the great European folly gardens.? Most of us who know Vizcaya well embrace this place for both its serious significance and its eccentric charm.\n\\n\n\\n[vc_empty_space height=\"30px\"][vc_btn title=\"Request Now\" style=\"flat\" shape=\"square\" color=\"chino\" size=\"lg\" link=\"url:http%3A%2F%2F0gl.c12.myftpupload.com%2Fcontact%2F|title:Contact|target:%20_blank|\"]",
